.comment {
  margin: 10px 0 10px 1em;
  background-color: var(--theme_commentbg);
  color: var(--theme_text);
  border: 1px solid var(--theme_border);
  border-radius: 3px;
  overflow: auto;
  max-height: 600px;
  padding: 16px;
}

.info {
  display: flex;
  width: 100%;
  gap: 1rem;
}

.commentId {
  color: var(--theme_id);
  font-size: 1rem;
  font-weight: bold;
}

.replies {
  background-color: var(--theme_bg);
}

@media (max-width: 480px) {
  .comment {
    padding: 8px;
    font-size: 1rem;
  }
}
